import React from "react";
import "./styles/Workbox.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEye } from "@fortawesome/free-solid-svg-icons";
import { faGithubAlt } from "@fortawesome/free-brands-svg-icons";

export default function Workbox(props) {
  return (
    <div className="box">
      <img src={props.pic} alt="screenshot" />
      <div className="highlight">
        <div className="buttons">
          <a type="button" class="btn btn-dark" href={props.link}>
            Go to {props.title}
          </a>

          <a type="button" class="btn btn-dark" href={props.code}>
            <FontAwesomeIcon
              icon={faGithubAlt}
              color="white"
              className="mr-2"
            />
            Check Code
          </a>
          <a type="button" class="btn btn-dark" href={props.screenshot}>
            <FontAwesomeIcon icon={faEye} color="white" className="mr-2" />
            Screenshot
          </a>
        </div>
      </div>
    </div>
  );
}
